.jumbotron > .overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(108, 122, 137, 0.55);
  z-index: 1;
}

.jumbotron > .inner {
  position: relative;
  z-index: 2;
}



@media screen and (max-width: 420px) {
  .iframe-container {
    overflow: hidden;
    padding-top: 56.25%;
    position: relative;
  }
   
  .iframe-container iframe {
     border: 0;
     height: 100%;
     left: 0;
     position: absolute;
     top: 0;
     width: 100%;
  }
}



ul{
  list-style: none;
}

